// Double inception (must go deeper gif)

/*
state,       // will be module local state if defined in a module.
getters,     // module local getters of the current module
rootState,   // global state
rootGetters  // all getters
*/

export function makeEnhancer(enhancerName, enhancer) {
  return (getter, options) => {

    let enhancedGetter;
    if ( !getter._enhancers ) {
      enhancedGetter = function ( state, getters, rootState, rootGetters ) {
        let getterName = enhancedGetter._vueGetterName;
        return enhancer(getterName, getter, [ state, getters, rootState, rootGetters ])
      }
      enhancedGetter._enhancers = [ enhancerName ]
    } else {
      // The getter is already enhanced
      let existingEnhancedGetter = getter;

      // We have to wrap enhancerGetter around the existing enhancer
      enhancedGetter = function (state, getters, rootState, rootGetters) {
        let getterName = enhancedGetter._vueGetterName;

        // Don't forget to updat the name (...)
        existingEnhancedGetter._vueGetterName = getterName;

        // Make the existing enhancer look like a getter
        let newGetter = (state, getters, rootState, rootGetters) => {
         // let res = existingEnhancedGetter(getterName, getter, [ state, getters, rootState, rootGetters ])
         return existingEnhancedGetter( state, getters, rootState, rootGetters )
        }

        return enhancer(getterName, newGetter, [ state, getters, rootState, rootGetters ])
      }

      // Add new enhancer to list of enhancers
      enhancedGetter._enhancers = existingEnhancedGetter._enhancers;
      enhancedGetter._enhancers.push(enhancerName);
    }
    return enhancedGetter;
  }
}

export function enhanceStore(obj, attachAll = null ) { // options?

  if ( attachAll ) {
    // TODO: attach enhancer to all getters
  }

  // Also support entire store
  let getterList = obj.getters || obj;
  for ( let getterName in getterList ) {
    // recursive?
    if ( getterList[getterName]._enhancers ) {
      getterList[getterName]._vueGetterName = getterName;
    }
  }
  // console.log(getterList)
  return getterList
}

