import Vue from 'vue'
import Vuex from 'vuex'

import { enhanceStore } from './enhancers'
import { log, logGroup, perfMean, perfOnce, count } from './enhancers/enhancers'

Vue.use(Vuex)

// Helpers
const touch = (...args) => {};
const simulateWork = (n) => {
  let x = 0;
  for ( let i = 1; i < n; i++ ) {
    x += 6 / ( i * i )
  }
  return x; // Hurray, we just calculated Math.PI ** 2
}

export default new Vuex.Store({
  state: {
    value_a: 0,
  },
  mutations: {
    SET_VALUE_A(state, value) {
      state.value_a = value;
    },
  },
  getters: enhanceStore({
    my_value1: log(count((state => state.value_a))),
    my_value2: logGroup(perfMean(count((state) => {
      touch(state.value_a);
      let x = simulateWork(10000000);
      return x - Math.PI ** 2;
    }))),
    my_value3: perfOnce(function foobar(state) {
      simulateWork(5000000);
      return state.value_a;
    })
  }),
})
