import {  makeEnhancer } from '.'

window._COUNTER = new Map();
window._PERF1   = new Map();
window._PERFM   = new Map();

export const log = makeEnhancer("LOG", (getterName, getter, getterArgs) => {
  console.log(`[${getterName}] LOG`);
  return getter(...getterArgs);
})

export const logGroup = makeEnhancer("GROUPLOG", (getterName, getter, getterArgs) => {
  let t0 = performance.now();
  let result = getter(...getterArgs);
  let t1 = performance.now();
  console.group(`[${getterName}]`);
  console.log("dt:", t1 - t0);
  console.log("ts:", performance.now());
  console.log("result:", result);
  console.groupEnd();
  return result;
})

export const count = makeEnhancer("COUNT", (getterName, getter, getterArgs) => {
  window._COUNTER = window._COUNTER || new Map();
  window._COUNTER[getterName] = ( window._COUNTER[getterName] || 0 ) + 1;
  // console.log(`[${getterName}] COUNT`,window._COUNTER[getterName])
  return getter(...getterArgs);
})

export const perfOnce = makeEnhancer("PERF1", (getterName, getter, getterArgs) => {
  let t0 = performance.now();
  let result = getter(...getterArgs);
  let t1 = performance.now();
  // console.log(`[${getterName}] PERF1`, Math.round(t1 - t0))
  window._PERF1 = window._PERF1 || new Map();
  window._PERF1[getterName] = t1 - t0; // TODO Mean value
  return result;
})

// Moving average
export const perfMean = makeEnhancer("PERFM", (getterName, getter, getterArgs) => {
  let t0 = performance.now();
  let result = getter(...getterArgs);
  let t1 = performance.now();
  let dt = t1 - t0;
  // console.log(`[${getterName}] PERFM`, Math.round(dt))
  window._PERFM = window._PERFM || new Map();
  const old_dt = window._PERFM[getterName] || dt;
  const alpha = 0.25;
  window._PERFM[getterName] = (1 - alpha) * old_dt + alpha * dt;
  return result;
})

